@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.BroadcastInfo {
    .ChannelLogo {
        position: relative;
        top: 2px;
    }

    &__container {
        width: 80%;
        display: inline-block;
        min-height: 20px;
        @include itv-font-type(subtitle-2);
    }

    .cp_episode-info {
        @include itv-font-type(body-large);

        .cp_icon {
            height: 18px;

            &__logo-itv-mono-neg.cp_icon--xsmall {
                width: 35px;
                height: auto;
            }
  
            &__logo-itv2-mono-neg.cp_icon--xsmall, &__logo-itv3-mono-neg.cp_icon--xsmall {
                width: 44px;
                height: auto;
            }
  
            &__logo-itvbe-mono-neg.cp_icon--xsmall {
                width: 64px;
                height: auto;
            } 
        }
    
        &__series-hierarchy, &__dash, .cp_text-separator {
            @include itv-font-type(body-large);
        }

        &__series-hierarchy {
            font-weight: 700;
        }
    }

    &__text {
        padding-top: $spacing-03;
    }
}
