@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.HubplusPromoLetterbox {
    &__heading {
        margin-left: 0;
        width: 800px;
        @include itv-font-type(title-2);
    }

    &__title {
        color: $color-itvx-text-secondary;
        margin: 0;
        @include itv-font-type(subtitle-2, bold);
    }

    &__logo {
        width: 95px;
    }

    &__text {
        color: $color-itvx-text-secondary;
        margin: 80px 0 0;
        @include itv-font-type(body-small);
    }
}
