@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.BritboxPromoLetterbox {
    &__heading {
        margin:  $spacing-05 0 $spacing-06 $spacing-03;
        display: inline-block;
        width: 300px;
    }

    &__title {
        margin: $spacing-01 0 $spacing-04 0;
        color: $color-britbox-brand-secondary;
        @include itv-font-type(title-3);
        text-transform: uppercase;
    }

    &__logo {
        width: 140px;
        display: inline-block;
        margin-top: -80px;
    }

    &__text {
        @include itv-font-type(cta);
        margin: $spacing-02 0 $spacing-02 0;
    }
}
