@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../../../styles/common';

.SimulcastLetterbox {
    &__now {
        margin-bottom: 0;
    }

    &__wrapper {
        width: 330px;
    }

    &__button {
        margin: $spacing-05 0 $spacing-04;
        width: 280px;
        text-align: center;

        &:hover {
            svg g {
                fill: $color-itvx-text-inverse;
            }
        }

        &.focus svg g {
            fill: $color-itvx-text-inverse;
        }
    }

    &__startAgainButton {
        margin-left: $spacing-04;
    }

    &__progress {
        margin-bottom: $spacing-05;
    }

    &__label {
        font-weight: 700;
    }

    &__label,
    &__time {
        display: inline-block;
        @include itv-font-type(body-small);
    }

    &__subheading {
        color: $color-itvx-neutrals-300;
        @include itv-font-type(subtitle-2, bold);
        margin: 0;
        margin-bottom: $spacing-06;
    }
}
