@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

@keyframes fadeIn {
    0% {
        opacity: 0;
    }

    100% {
        opacity: $opacity-06;
    }
}


.Letterbox {
    $parent: &;
    position: relative;
    height: calc(340px - #{$app-padding-top});
    z-index: 1;
    background-color: $color-itvx-background-primary;

    &__heading {
        margin: 0 0 $spacing-03 0;
        @include itv-font-type(title-2);
    }

    &__tag {
        padding-top: 0;
        margin-bottom: $spacing-03;

        &--simulcast {
            @include itv-font-type(body-large);

            .cp_tag__icon {
                position: relative;
                top: 3px;

                svg {
                    width: 22px;
                    height: 18px;

                    path {
                        stroke: white;
                    }
                }
            }
        }
    }

    &__desc {
        @include itv-font-type(body);
    }

    &__details {
        z-index: 4;
    }

    &__image {
        z-index: 2;
        position: absolute;
        top: -$app-padding-top;
        right: -$app-padding-right;
        width: 603px;
        height: 340px;

        .animations & {
            opacity: 0;
            animation-name: fadeIn;
            animation-duration: $motion-duration-03;
            animation-fill-mode: forwards;
            animation-timing-function: $motion-easing-04;
        }

        picture {
            height: 100%;
        }
    }

    &__premium {
        z-index: 2;
        top: -$app-padding-top;
        left: -48px;
        position: absolute;
        width: 1156px;
    }

    &__channel-logo {
        bottom: 78px;
        position: absolute;
        right: 0;
        z-index: 3;

        .cp_channel-logo {
            width: 294px;
        }
    }

    &__content-tag {
        color: $color-itvx-brand-primary;
        padding-bottom: $spacing-03;
        @include itv-font-type(subtitle-2);
    }

    &__synopsis {
        margin-bottom: $spacing-05;
    }

    &__gradient {
        position: absolute;
        top: -$app-padding-top;
        left: -2px;
        width: 100px;
        height: calc(100% + #{$app-padding-top});
        z-index: 3;
        background: $color-itvx-background-primary;
    }

    &__now {
        margin-bottom: $spacing-06;

        #{$parent}__label {
            margin-bottom: $spacing-02;
        }

        #{$parent}__time {
            display: inline-block;
            @include itv-font-type(subtitle-2);
        }
    }

    &__next {
        #{$parent}__time {
            @include itv-font-type(body-small);
        }
    }

    &__subheading {
        @include itv-font-type(subtitle-2);
        margin: 0;
    }

    &__label {
        color: $color-itvx-neutrals-300;
        @include itv-font-type(body-small);
    }

    &__time {
        color: $color-itvx-neutrals-300;
    }

    &__time-left {
        font-weight: 700;
    }
}
