@import '~@itv/fe-components/cjs/common/styles/ctv-overrides';
@import '../../styles/common';

.ViewAllTile {
    display: block;
    height: 136px;
    margin-left: $spacing-02;
    padding-top: $focus-border-width;
    padding-right: 800px;

    &.focus {
        text-decoration: none;

        .ViewAllTile__wrapper {
            background-color: $color-itvx-brand-primary;
            border: 3px solid $color-itvx-brand-primary;
        }
    }

    &__wrapper {
        color: $color-itvx-text-inverse;
        border: $focus-border-width solid transparent;
        background-color: $color-itvx-neutrals-100;
        height: 100%;
        width: 250px;
    }

    &__content {
        align-items: flex-end;
        display: flex;
        @include itv-font-type(body);
        height: 100%;
        padding-left: $spacing-06;
    }

    &__copy {
        margin-bottom: $spacing-06;

        .CategorySlider__category, .CollectionSlider__category {
            @include itv-font-type(subtitle-2);
            display: block;
        }
  
    }
}

.animations {
    .ViewAllTile__wrapper {
        transition: transform $motion-duration-01 ease-out;
    }
}
